import React from 'react';
import ReactEcharts from 'echarts-for-react';
import 'echarts/theme/macarons';

const pictorialBar1 = {};

let spirit = 'image://data:image/png;base64,';

let maxData = 2000;

pictorialBar1.option = {
  tooltip: {
  },
  xAxis: {
    max: maxData,
    splitLine: {show: false},
    offset: 10,
    axisLine: {
      lineStyle: {
        color: '#999'
      }
    },
    axisLabel: {
      margin: 10
    }
  },
  yAxis: {
    data: ['2013', '2014', '2015', '2016'],
    inverse: true,
    axisTick: {show: false},
    axisLine: {show: false},
    axisLabel: {
      margin: 10,
      textStyle: {
        color: '#999',
        fontSize: 16
      }
    }
  },
  grid: {
    top: 'center',
    height: 200,
    left: 70,
    right: 100
  },
  series: [{
    // current data
    type: 'pictorialBar',
    symbol: spirit,
    symbolRepeat: 'fixed',
    symbolMargin: '5%',
    symbolClip: true,
    symbolSize: 30,
    symbolBoundingData: maxData,
    data: [891, 1220, 660, 1670],
    markLine: {
      symbol: 'none',
      label: {
        normal: {
          formatter: 'max: {c}',
          position: 'start'
        }
      },
      lineStyle: {
        normal: {
          color: 'green',
          type: 'dotted',
          opacity: 0.2,
          width: 2
        }
      },
      data: [{
        type: 'max'
      }]
    },
    z: 10
  }, {
    // full data
    type: 'pictorialBar',
    itemStyle: {
      normal: {
        opacity: 0.2
      }
    },
    label: {
      normal: {
        show: true,
        formatter: function (params) {
          return (params.value / maxData * 100).toFixed(1) + ' %';
        },
        position: 'right',
        offset: [10, 0],
        textStyle: {
          color: 'green',
          fontSize: 18
        }
      }
    },
    animationDuration: 0,
    symbolRepeat: 'fixed',
    symbolMargin: '5%',
    symbol: spirit,
    symbolSize: 30,
    symbolBoundingData: maxData,
    data: [891, 1220, 660, 1670],
    z: 5
  }]
};


const Chart = () => (
  <div className="box box-default mb-4">
    <div className="box-header">Basic PictorialBar</div>
    <div className="box-body">
      <ReactEcharts option={pictorialBar1.option} theme={"macarons"} style={{height: '400px'}}/>
    </div>
  </div>
)

export default Chart;
